@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";
@import "@material/typography/_mixins.scss";

:global{
	.mdc-tab-bar{
		margin:0;
		width: 100%;
		height: 44px;
		border-bottom: 1px solid;
		@include mdc-theme-prop(border-bottom-color, text-hint-on-background);
		&__indicator{
			@include mdc-theme-prop(background-color, text-link-on-light);
		}
	}
	.mdc-tab{
		@include mdc-theme-prop(color, text-primary-on-background);
		font-size: 13px;
		font-weight: map-get($mdc-typography-font-weight-values, bold);
		&--active{
			@include mdc-theme-prop(color, text-link-on-light);
		}
	}
}


