@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";
@import "@material/typography/_mixins.scss";

:global{
	.row-base{
	 	.component-wrap{
	 		padding: 0.75rem 0;
	 	}
 	}
 	.column-base{
 		flex: 0 0 40%;
		margin-left:0.75rem;
		display: flex;
		flex-direction: column;
		width: 0;
	 	.mdc-card{
			box-shadow: none;
			@include justify-content(flex-start);
			border-radius: 0;

			&__media{
				padding: 0;
				height: 95px;
				position: relative;
				&-item{
					margin: 0;
					@include center;
					height: 95px;
				}
			}
			&__primary{
				padding: 0;
			}
			&__title--large{
				font-size:  map-get($mdc-typography-font-size-values, small);
				font-weight: map-get($mdc-typography-font-weight-values, bold);
				line-height: normal;
				@include mdc-typography-overflow-ellipsis;
			}
			&__subtitle{
				@include mdc-theme-prop(color, text-secondary-on-light); 
				font-size:  map-get($mdc-typography-font-size-values, smaller);
				margin-top: 3px;
				@include mdc-typography-overflow-ellipsis;
			}
			&__supporting-text{
				font-size:  map-get($mdc-typography-font-size-values, small);
				font-weight: map-get($mdc-typography-font-weight-values, bold);
				color:$mdc-theme-primary;
				padding: 0 !important;
				margin: 0 !important;
			}
		}
	}
}


