@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";


:global{
	.recent-activity{
		box-shadow:none;
		flex: 0 0 86%;
		@include justify-content(flex-start);
		margin: 0 1rem 0 0;
		padding: 0.75rem;
		border-radius: 0;
		.mdc-card__title{
			@include flexbox;
			margin-bottom: 0.5rem;
			font-weight: map-get($mdc-typography-font-weight-values, bold);
			a{
				font-size: map-get(map-get($mdc-typography-styles, caption), font-size); 
				margin-left: auto;
				svg{
					width: 12px;
					fill: $mdc-theme-primary;
					line-height: 14px;
					margin-bottom: -2px;
				}
			}
		}
		.mdc-card__horizontal-block{
			padding: 0;
				.mdc-card__media-item{
					height: 3.125rem;
					margin:0;
				}
				.mdc-card__supporting-text{
					line-height: 0.875rem;
					padding: 0 0 0 0.625rem;
					
					p{
						font-size: map-get(map-get($mdc-typography-styles, caption), font-size); 
						strong{
							font-weight: map-get($mdc-typography-font-weight-values, bold);
						}
					}
					.mdc-card__actions{
						padding:10px 0 0 0;
						button{
							text-transform: none;
							letter-spacing: normal;
						}
					}
				}
		}
	}
}


