@import "@material/typography/_mixins.scss";
@import "@material/typography/_variables.scss";
@import "@material/theme/variables";
@import "@material/theme/mixins";

:global{
	.mdc-toolbar{
		&__row{
			min-height: 3.125rem;
		}
		&__icon--menu+.mdc-toolbar__title{
			margin-left: 0;
		}
		&__black {
			background: $mdc-theme-black;
		}
		&__title{
	  		display: inline-block;
	  		font-size: 0.9375rem;
	  		font-weight: map-get($mdc-typography-font-weight-values, bold);
	  		padding: 0 0.6875rem 0 0;
	  		position: relative;
	  	}
		&__icon--menu{
	  		padding: 1rem  0.5rem 1rem 0.875rem;
	  	}
	  	&__arrow{
		  	@include arrow(down, $mdc-theme-background, 0.25rem);
			position: absolute;
	  		@include center(false, true);
	  		right: 0;
		}

		&__icons{
	  		@include mdc-theme-prop(fill, text-primary-on-dark);
	  		height: 1rem;
	  		width: 1rem;
	  	}
	  	&__section{
	  		align-items: center;
	  	}
  	}
  	.mdc-toolbar__section--align-end{
  		padding-right: 0.875rem;
  		.mdc-button{
  			background-color: $mdc-theme-yellow;
  			border-radius: 0;
  			@include mdc-theme-prop(color, text-primary-on-light);
  			font-size: 0.625rem;
  			font-weight: map-get($mdc-typography-font-weight-values, bold);
  		}
  	}
  	.logo{
  		@include mdc-theme-prop(background-color, text-primary-on-dark);
  		display: inline-block;
  		margin-right: 0.3125rem;
  		@include circle(1.625rem);
  		text-align: center;
  		.mdc-toolbar__icons{
  			fill: $mdc-theme-secondary;
  			position: relative;
  			top: 0.25rem;
  		}
  	}
}



