@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";
@import "@material/typography/_mixins.scss";

:global{
	.spotlights{
		flex-direction: column;
		padding-top: 12px;
		.scroll__x-axis{
			padding: 12px 0;
			background: $mdc-theme-bg-color;
		}
		.mdc-button{
			text-transform: none;
			font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
			font-weight: map-get($mdc-typography-font-weight-values, bold);
			text-align: left;
			@include mdc-theme-prop(background-color, text-primary-on-dark);
			display: flex;
			padding: 0 12px;
			height: 44px;
			span{
				margin-left: auto;
				font-weight: 100;
				font-size: map-get(map-get($mdc-typography-styles, headline), font-size);
				@include mdc-theme-prop(color, text-secondary-on-light);
			}
		}
	}
}


