@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";

:global{
	.category-box{
		display: flex;
		flex-wrap:wrap;
		padding: 0;
		flex: 1;
		.mdc-list-item{
			border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.12);
			border-right: 0.0625rem solid rgba(0, 0, 0, 0.12);
		    flex: 0 0 25%;
			min-height: 82px;
			justify-content: center;
			letter-spacing: normal;
			display: flex;
			flex-direction: column;
		    .category-icons{
		    	height: 38px;
				svg{
					width: 33px;
				}
			}
			label{
				color: map-get(map-get($mdc-theme-text-colors, dark), primary);
				font-size: 0.6875rem;
				line-height: normal;
				text-align: center;
			}
			&:nth-child(4n){
				border-right: 0;
			}
			&:nth-last-child(-n+4){
				border-bottom: 0;
			}
		}
		.top-categories{
			display: flex;
			flex-wrap: wrap;
		}
		.showHide{
			@extend .top-categories;
			-moz-transition: 0.3s;
		    -ms-transition: 0.3s;
		    -o-transition: 0.3s;
		    -webkit-transition: 0.3s;
		    transition: 0.3s;
		    max-height: 0;
		    overflow: hidden;
		  	a{
		  		border-top: 0.0625rem solid rgba(0, 0, 0, 0.12);
				border-bottom: 0;
			}
			&.show{
				max-height:400px;
			}
		}
  	}
}



