@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";

input[type="search"] {
    -webkit-appearance: textfield;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button {
    -webkit-appearance: none;
}

:global{
	.badge[data-badge]::after{
	  	content: attr(data-badge);
		display: flex;
	    align-items: center;
	    justify-content: center;
	    position: absolute;
	    top: -2px;
	    right: -8px;
	    font-weight: map-get($mdc-typography-font-weight-values, bold);
	    font-size: 0.5625rem;
	    min-width: 14px;
	    height: 14px;
	    line-height: 1rem;
	    border-radius: 1.25rem;
	    @include mdc-theme-prop(color, text-primary-on-primary);
	    @include mdc-theme-prop(background-color, text-error-on-light);
	    font-style: normal;
	    padding: 1px 3px;
	    border: 1px solid;
	    @include mdc-theme-prop(border-color, text-primary-on-primary);
	}
	
	.component-wrap{
		background-color: var(--mdc-theme-text-primary-on-dark, white);
		border-bottom: 1px solid rgba(0, 0, 0, 0.12);
		display: flex; 
		margin-bottom: 1.25rem;
	}
	.scroll__x-axis{
		@include flexbox;
		scroll-behavior: smooth;
		overflow-x: auto;
  		width: 100%;
   		-webkit-overflow-scrolling: touch;
  		&::-webkit-scrollbar{ 
  			display: none;
		}
	}
	.section-title{
		color: map-get(map-get($mdc-theme-text-colors, dark), primary);
		@include flexbox;
		font-weight: map-get($mdc-typography-font-weight-values, medium);
		padding:0 0.75rem;
		margin-bottom: 10px;
		font-weight: map-get($mdc-typography-font-weight-values, bold);
		a{
			color:$mdc-theme-primary;
			font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
			margin-left: auto;
		}
 	}

}





