@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";

:global {
	.mdc-footer {
		@include mdc-theme-prop(color, text-secondary-on-light);
		padding: 0.75rem 0.75rem 1.875rem 0.75rem;
	  	
	  	&__heading {
	    	@include mdc-theme-prop(color, text-secondary-on-light);
	    	font-weight: map-get($mdc-typography-font-weight-values, bold);
	    	font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
	    	margin: 0 0 0.625rem;
	    	text-transform: map-get(map-get($mdc-typography-styles, button), text-transform);
	    }

		&__primary-links {
		  	padding: 0;
			
			a {
		      	color: inherit;
		      	@include inline-flex;
				font-size: map-get($mdc-typography-font-size-values, small);
		    	height: auto;
		    	line-height: map-get(map-get($mdc-typography-styles, body2), line-height);
		    	text-decoration: map-get(map-get($mdc-typography-styles, button), text-decoration);
		    	
				&:not(:last-of-type):after{
		      		@include mdc-theme-prop(color, text-hint-on-light);
		      		content: "|";
					font-size: map-get(map-get($mdc-typography-styles, subheading2), font-size);
		      		font-weight: map-get($mdc-typography-font-weight-values, light);
		        	padding: 0 0.3125rem;
		        }
		    }
		}

		&__socials{
	  		margin: 1.5625rem 0 1.75rem 0;

	  		.social-elements{
	    		@include flexbox();
	    	}
	  
	  		.mdc-fab {
	      		@include mdc-theme-prop(background-color, text-secondary-on-light);
	      		box-shadow: none;
	      		height: 1.875rem;
	      		margin: 0 0.9375rem 0 0;
	      		min-width: 1.875rem;
	      		width: 1.875rem;
	      		
	      		&:last-of-type {
	        		margin-right: 0;
	      		}
	      		
	      		&__icon {
	        		font-size: map-get(map-get($mdc-typography-styles, subheading2), font-size);
	        		position: relative;
	      		}
	    	}
		}
		
		&__copyright{
	  		@include mdc-theme-prop(color, text-secondary-on-light);
	  		font-size: map-get($mdc-typography-font-size-values, smaller);
	  		margin: 0;
	  		padding: 0;
		}

		&__icons{
			@include mdc-theme-prop(fill, text-primary-on-primary);
			height: 1.125rem;
			width: 1.125rem;
		}
	}
}


