@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";

:global{
	.toolbar-search{
		@include mdc-theme-prop(background-color, primary);
		padding: 0.125rem 0.875rem 0.5rem;
		&__child{
			align-items: center;
			@include mdc-theme-prop(background-color, text-primary-on-primary);
			border-radius: 0.125rem;
			display: flex;
  			flex-direction: row;
  			padding: 0.5rem;
			position: relative;
		}
		&__icon, input, .action-box{
			min-height: 1.25rem;
		}
		&__icon{
			flex: 0 0 0.75rem;
			@include mdc-theme-prop(fill, text-disabled-on-background);
			height: 1.25rem;
		}
		&__field{
			background-color: transparent;
			border: 0 none;
			flex: 1 1 auto;
			font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
			outline: 0;
			padding: 0 0.5rem;
		}
		input[type="search"] {
		    -webkit-appearance: textfield;
		}
		input[type="search"]::-webkit-search-decoration,
		input[type="search"]::-webkit-search-cancel-button {
		    -webkit-appearance: none;
		}
		.action-box{
			border-left: 1px solid;
			@include mdc-theme-prop(border-left-color, text-hint-on-background);
			display: flex;
			flex: 0 0 30px;
			justify-content: center;
			padding-left: 0.3rem;
			i{
				display: flex;
				position: relative;
			}
			&__icons{
				@include mdc-theme-prop(fill, text-help-on-light);
				height: 1.25rem;
				width: 1.0625rem;
			}
		}
	}
	.page-layer {
		.mdc-toolbar__title{
			display: flex;
			flex: 1;
			padding: 0;
		}
		.toolbar-search {
			background-color: transparent;
			display: flex;
			flex: 1;
			padding: 0;
			&__field{
				@include mdc-theme-prop(color, text-primary-on-primary);
				font-weight: map-get($mdc-typography-font-weight-values, medium);
				font-size: map-get(map-get($mdc-typography-styles, subheading2), font-size);
				&::placeholder{
					@include mdc-theme-prop(color, text-primary-on-primary);
					opacity: .5;
				}
			}
		}
	}





}






