@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";

:global{
	.experience-app-box{
	  @include background-gradient(#007EBE, #21C4CD, horizontal);
	  color:$mdc-theme-background;
	  height: 132px;
	  padding: 0.9375rem;
	  position: relative;
	  h3{
	    margin: 10px 0 2px 0 !important;
	    color: $mdc-theme-background;
	    font-weight: map-get($mdc-typography-font-weight-values, bold);
	  }
	  p{
	    color: rgba(255,255,255, .85);
	    font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
	  }
	  .mdc-button{
	    width: 107px;
	    height: 32px;
	    text-indent: -9999px;
	    margin-top: 13px;
	  }
	}
	.app-stores, .app-screen{
	  background: url(../../../../../assets/images/app_store_sprite.png) 0 0 no-repeat;
	  background-size: 197px;
	  border: 0 none;
	  display: inline-block;
	  position: relative;
	}
	.google-play-btn{
	  background-position: -1px -120px;
	}

	.apple-store-btn{
	  background-position: -111px -120px;
	  width: 86px;
	}

	.app-screen{
	  position: absolute;
	  right: 10%;
	  bottom: 0;
	  &.apple{
	    background-position: 0 -3px;
	    width: 93px;
	    height: 114px;
	  }
	  &.android{
	    background-position: -95px 0px;
	    width: 86px;
	    height: 117px;
	  }
	}
}


