@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";

:global{
	.alert-box{
	    padding: 0.875rem;
		align-items: center;
	    figure{
		    border: 2px solid #ffdb0c;
		    flex: 0 0 2.1875rem; 
		    padding: 2px;
		    @include circle(100%);
		    .inner-circle{
		    	border:solid 2px;
		    	@include mdc-theme-prop(border-color, text-lightGrey-on-light);
		    	@include circle(100%);
			    height: 1.75rem;
			    width: 1.75rem;
			    line-height: 2.0625rem;
			    text-align: center;
			    svg{
			    	width: 18px;
    				height: 18px;
    				@include mdc-theme-prop(fill, text-help-on-light);
    			}
		    }
		}
		p{
			color: map-get(map-get($mdc-theme-text-colors, dark), primary);
    		font-size:13px;
    		padding: 0 0 0 10px;
    		flex: 0 0 56%;
    		strong{
    			font-weight: map-get($mdc-typography-font-weight-values, bold);
    		}
		}
		.mdc-button{
			margin-left: auto;
			text-transform: none;
			padding: 0;
			font-size: 11px;
			font-weight: 600;
			padding: 0 8px;
		}
	}
}


