@import "@material/theme/variables";
@import "@material/theme/mixins";

:global{
	.mdc-button--stroked:not(:disabled){
		border-color: $mdc-theme-primary;
	    border-color: var(--mdc-theme-primary, $mdc-theme-primary);
	}
	.mdc-button--stroked {
	    border-style: solid;
	    padding: 0 14px;
	    border-width: 1px;
	    line-height: 32px;
	    color: $mdc-theme-primary;
	}
	.mdc-button{
		border-radius: 2px;
		&__no-border-radius{
				border-radius:0;
		}
		&__width-full{
			width:100%;
		}
		&__width-half{
			width:50%;
		}
		&--accent{
			&.mdc-button--stroked:not(:disabled){
				color: $mdc-theme-secondary;
	    		border-color: var(--mdc-theme-secondary, $mdc-theme-secondary);
			}
		}
	}
}



