@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";
@import "@material/typography/_mixins.scss";

:global{
  .quikr-collection{
    position: relative;
      @include background-gradient(#CAA5A6, #6F94AE, radial);
      display: block;
      padding: 25px 0 20px 12px;
      h3{
        @include mdc-theme-prop(color, text-primary-on-dark);
        margin-bottom: 12px;
        padding: 0;
      }
      .scroll__x-axis{
        @include mdc-theme-prop(background-color, text-primary-on-dark);
        padding: 12px 0 12px;
      }
      .mdc-list{
        display: flex;
        padding: 0;
      }
      .mdc-list-item{
        flex-direction: column;
        height: auto;
        position: relative;
        text-align: center;
        width: 130px;
        figure{
          border-right: 1px solid;
          @include mdc-theme-prop(border-color, text-hint-on-light);
          position: relative;
          width: 100%;
          height: 85px;
          padding-top: 10px;
        }
        label{
          font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
          font-weight: map-get($mdc-typography-font-weight-values, bold);
          display: block;
          @include mdc-typography-overflow-ellipsis;
          padding: 0 10px;
          line-height: normal;
          width: 100%;
        }
      }
      .collection-icon{
        position: absolute;
        top: 13px;
        right: 13px;
        width: 35px;
        svg{
          fill-opacity:0.4;
        }
      }
    }
  }


