@import "@material/typography/_variables.scss";
@import "@material/theme/variables";
@import "@material/theme/mixins";

:global {
	.mdc-temporary-drawer__header{
	  	@include background-gradient(#007EBE, #21C4CD, horizontal);
		&:before{
		  padding: 0;
		}
	    &-content{
	      align-items: center;
	      padding: 0.625rem;
	      position: relative;
	      a{
	      	align-items: center;
	      	@include flexbox;
	      	text-decoration: map-get(map-get($mdc-typography-styles, button), text-decoration);	
	      }
	      figure{
	       	@include circle(3rem);
	       	border: 1px solid;
			@include mdc-theme-prop(border-color, text-primary-on-dark); //White
	       	@include mdc-theme-prop(background-color, text-lightGrey-on-light);
	       	margin: 0;
	       	overflow: hidden;
	       	position: relative;
	       	i{
	       		@include center;
	         	font-style: normal;
	         	font-size: map-get(map-get($mdc-typography-styles, title), font-size);
	         	@include mdc-theme-prop(color, text-secondary-on-light); //666
	       	}
	       	img{
	           width: 100%;
	       	}
	     }
	     .details{
	       @include mdc-theme-prop(color, text-primary-on-dark); //White
	       margin-left: 0.625rem;
	         p{
	           font-size: map-get(map-get($mdc-typography-styles, subheading2), font-size);
	           font-weight: map-get($mdc-typography-font-weight-values, bold);
	           margin: 0 0 0.1875rem 0;
	         }
	         span{
	           font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
	         }
	     }
	}
	  .arrow-right{
	  	@include arrowHead(right, rgba(255,255,255,.8), 0.125rem);
		height: 0.5rem;
	  	width: 0.5rem;
  		position: absolute;
  		right: 0.75rem;
	  	}
	}	
}



