@import "@material/theme/variables";
@import "@material/theme/mixins";
@import "@material/typography/variables";

:global{
	.search__suggester{
		height: 100%;
		overflow-y: auto;
		h5{
			padding: 1.25rem 1rem 0.75rem;
			font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
			font-weight: map-get($mdc-typography-font-weight-values, medium);
			@include mdc-theme-prop(color, text-secondary-on-light);
			display: flex;
			a{
				margin-left: auto;
			}
		}
		.mdc-list{
			padding: 0;
			@include mdc-theme-prop(background-color, text-primary-on-dark);
		}
		.mdc-list-item{
			padding: 0 12px 0;
			display: flex;
			font-size: map-get(map-get($mdc-typography-styles, subheading1), font-size);
			min-height: 2.625rem;
			height: auto;
			border-bottom: 1px solid rgba(0, 0, 0, .07);
			&:last-child{
				border-bottom-color: rgba(0,0,0, .14);
			}
		}

		&--icon{
			flex: 0 0 2rem;
			height: 1rem;
			align-items: flex-start;
			justify-content: flex-start;
			@include mdc-theme-prop(fill, text-secondary-on-light);
			margin-right: 0.25rem;
		}
		p{
			flex: 1 0 0;
			line-height: 1.125rem;
			label{
				display: flex;
				flex: 1 1 0;
				font-size: map-get(map-get($mdc-typography-styles, caption), font-size);
				@include mdc-theme-prop(color, text-disabled-on-background);
			}
			span{
				font-weight: map-get($mdc-typography-font-weight-values, regular),
			}
		}
	}
	.toolbar-search__suggestions{
		.mdc-list-item{
			min-height: 3rem;
			padding: 0 20px;
		}
	}
}


