@import "@material/typography/_mixins.scss";
@import "@material/typography/_variables.scss";
@import "@material/theme/variables";
@import "@material/theme/mixins";

:global {
  .mdc-temporary-drawer {
    .mdc-list-item{
      height: auto;
      min-height: 2.8125rem;
      line-height: 1.2rem;
      padding: 0rem 0.875rem;
      .arrow-right{
        @include arrowHead(right, #666, 0.125rem);
        height: 0.5rem;
        width: 0.5rem;
        position: absolute;
        right: 0.75rem;
      } 
      p{
        margin: 0;
        padding: 0;
      }
      
      &__start-detail{
        @include mdc-theme-prop(color, text-icon-on-light);
        margin-right: 1.5625rem;
        width: 0.9375rem;
      }
      &__header{
        @include mdc-theme-prop(color, text-primary-on-background);
        font-size: map-get(map-get($mdc-typography-styles, subheading2), font-size); 
        font-weight: map-get($mdc-typography-font-weight-values, medium);
        min-height: 3rem;
        .mdc-temporary-drawer__menu-icons{
          width: 0.875rem;
        }
      }
      >a{
          @include mdc-theme-prop(color, text-primary-on-background);
          display: flex;
          flex: 1;
          align-items: flex-end;
          > i{
            width: 30px;
            margin-right: 12px;
          }
      }
    }
    &__title {
        :global .mdc-temporary-drawer & {
        color: map-get(map-get($mdc-theme-text-colors, dark), disabled);
        font-size: map-get($mdc-typography-font-size-values, smaller);
        min-height: 1.875rem;
        padding: 0.625rem 0.75rem 0;
        text-transform: map-get(map-get($mdc-typography-styles, button), text-transform);
      }
    }
    &__sub-cates{
      color: map-get(map-get($mdc-theme-text-colors, dark), help);
      display: block;
      font-size: map-get($mdc-typography-font-size-values, smaller);
      @include mdc-typography-overflow-ellipsis;
      width: 50%;
    }
    &__menu-icons{
      height: 100%;
      fill: map-get(map-get($mdc-theme-text-colors, dark), help);
      width: 1rem;
    }
    &__category-icons{
      width: 28px;
    }
    .mdc-list__category{
      padding-top: 0;
    }
  }
}



